@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-light.woff2") format("woff2"), url("../fonts/roboto-condensed-light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "AlumniSans";
  font-display: swap;
  src: url("../fonts/alumni-sans-semi-bold.woff2") format("woff2"), url("../fonts/alumni-sans-semi-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "AlumniSans";
  font-display: swap;
  src: url("../../fonts/alumni-sans-extra-bold.woff2") format("woff2"), url("../fonts/alumni-sans-extra-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  color: #102654;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.link {
  text-decoration: none;
  cursor: pointer;
}

.button {
  display: flex;
  width: fit-content;
  height: fit-content;
  align-items: center;
  padding: 20px;
  font-family: inherit;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0);
  outline: 4px solid #ffffff;
  border: none;
  user-select: none;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}

.button:hover,
.button:focus,
.button:active {
  outline: none;
  background: linear-gradient(97.05deg, #ED0233 17.6%, #003EB7 95.31%);
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 103px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  padding-top: 30px;
  padding-bottom: 21px;
  background: conic-gradient(from 150.87deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
}

.header__content {
  display: grid;
  grid-template-columns: 1fr 1.77fr;
  gap: 64px;
  transform: translate(0, -30px);
}

.header__content--main {
  display: grid;
  margin-top: 16px;
  grid-template-columns: 1fr 1fr;
  gap: 0 17px;
}

.header__content--list {
  display: flex;
  flex-direction: column;
  align-self: center;
  margin-top: 102px;
  grid-row: 1/4;
}

.header__content--item {
  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
  color: #ffffff;
}

.header__content--main p {
  margin-bottom: 15px;
  font-family: "AlumniSans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  text-align: center;
  align-self: self-end;
  letter-spacing: 0.03em;
  color: #ffffff;
  transform: translate(32px, 4px);
}

.header__content--main h1 {
  grid-column: 2;
  margin-top: 15px;
  margin-bottom: 0;
  font-family: "AlumniSans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  -webkit-text-stroke: 3px #fff;
  color: #ffffff;
  transform: translate(0, -11px);
}

.header__content--button {
  grid-column: 2;
  display: flex;
  width: 92%;
  margin-left: 18px;
  justify-content: center;
  transform: translate(0px, -2px);
}

@media (max-width: 1023px) {
  .header {
    padding-top: 34px;
    padding-bottom: 95px;
    background: conic-gradient(from 155.49deg at 20.7% 35.74%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
  }
  .header__content {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    transform: translate(0, 13px);
  }
  .header__content--main {
    margin-top: -12px;
    gap: 0 25px;
  }
  .header__content--list {
    margin-top: 56px;
  }
  .header__content--item {
    font-size: 14px;
    line-height: 21px;
  }
  .header__content--main p {
    margin-bottom: 7px;
    text-align: end;
    font-size: 34px;
    line-height: 41px;
    transform: translate(-4px, 0);
  }
  .header__content--main h1 {
    width: min-content;
    margin-left: auto;
    margin-top: 7px;
    margin-bottom: 5px;
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
    transform: translate(0, -7px);
  }
  .header__content--button {
    margin: auto;
    padding: 19px;
    width: 100%;
    transform: translate(0, -14px);
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 14px;
    padding-bottom: 96px;
  }
  .header__content {
    grid-template-columns: 1fr;
  }
  .header__content--image {
    display: none;
  }
  .header__content--main {
    margin-top: 23px;
    gap: 0;
  }
  .header__content--list {
    transform: translate(-3px, 19px);
    width: 110%;
  }
  .header__content--main p {
    font-size: 28px;
    line-height: 34px;
  }
  .header__content--main h1 {
    margin-bottom: 26px;
    font-size: 72px;
    line-height: 86px;
    transform: translate(0, 0);
  }
  .header__content--button {
    grid-column: 1/-1;
    padding: 20px;
    transform: translate(0, 0);
  }
}
.nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  z-index: 1;
}

.nav__logo {
  color: #ffffff;
  transition: all 0.5s ease-in-out;
}

.nav__phone {
  font-family: "AlumniSans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
  transition: all 0.5s ease-in-out;
}

.nav__logo:hover,
.nav__phone:hover,
.nav__logo:focus,
.nav__phone:focus,
.nav__logo:active,
.nav__phone:active {
  color: rgba(16, 38, 84, 0.89);
  transform: scale3d(1.02, 1.02, 1.02);
}

@media (max-width: 1023px) {
  .nav__logo svg {
    width: 80px;
    height: auto;
  }
  .nav__phone {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .nav__logo svg {
    width: 67px;
  }
  .nav__phone {
    font-size: 20px;
    line-height: 24px;
    transform: translate(0, 2px);
  }
  .nav__phone:hover,
.nav__phone:focus,
.nav__phone:active {
    transform: scale3d(1.02, 1.02, 1.02) translate(0, 2px);
  }
}
.gym {
  padding-top: 120px;
  padding-left: 60px;
}

.gym__container {
  display: grid;
  grid-template-columns: 1fr 2.1fr;
}

.gym__content h2 {
  margin-top: 39px;
  margin-bottom: 8px;
  font-family: "AlumniSans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  color: inherit;
}

.gym__content h4 {
  margin-top: 8px;
  margin-bottom: 38px;
  font-family: inherit;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: inherit;
}

.gym__content p {
  margin-bottom: 8px;
  margin-top: 8px;
  font-family: inherit;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: inherit;
}

.gym__image {
  position: relative;
}

.gym__image-main img {
  margin-left: auto;
}

.gym__image-video {
  transform: translate(-2.4%, -56.5%);
}

.gym__image-video figure {
  position: relative;
  width: fit-content;
}

.gym__image-video-background {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 54, 122, 0.5);
}

.gym__image-video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  z-index: 1;
  width: 72px;
  height: 72px;
  background: linear-gradient(44.89deg, #003D85 25.38%, #B80027 74.89%);
  border: none;
  border-radius: 50px;
  outline: none;
  transform: translate(-50%, -50%);
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}

.gym__image-video__button::after {
  content: "";
  position: absolute;
  left: -20%;
  top: -20%;
  display: block;
  z-index: 1;
  width: 100px;
  height: 100px;
  background: linear-gradient(80deg, #ed0233 20%, #003eb7 90%);
  mask: radial-gradient(48px, rgba(255, 255, 255, 0) 100%, #ffffff);
  border: none;
  border-radius: 50px;
  opacity: 0.2;
  outline: none;
  transition: all 0.5s ease-in-out;
}

.gym__image-video__button:hover,
.gym__image-video__button:focus,
.gym__image-video__button:active {
  transform: scale3d(1.02, 1.02, 1.02) translate(-50%, -50%);
}

.gym__image-video__button:hover::after,
.gym__image-video__button:focus::after,
.gym__image-video__button:active::after {
  opacity: 0.4;
  mask: radial-gradient(35px, rgba(255, 255, 255, 0) 100%, #ffffff);
}

@media (max-width: 1023px) {
  .gym {
    padding-top: 62px;
    padding-left: 0;
  }
  .gym__container {
    grid-template-columns: 1fr;
    gap: 43px;
  }
  .gym__content {
    text-align: center;
  }
  .gym__content h2 {
    margin-bottom: 0;
    font-size: 42px;
    line-height: 50px;
    transform: translate(-8px, 0);
  }
  .gym__content h4 {
    margin-top: 0;
    margin-bottom: 32px;
    transform: translate(0, -1px);
  }
  .gym__content p:not(:first-of-type) {
    display: none;
  }
  .gym__content p {
    margin: auto;
    width: 75%;
  }
  .gym__image-main img {
    margin: auto;
    width: 100%;
    height: auto;
  }
  .gym__image-video {
    transform: translate(0%, -50.5%);
  }
  .gym__image-video figure {
    margin: auto;
  }
  .gym__image-video img {
    width: 266px;
    height: auto;
  }
  .gym__image-video iframe {
    width: 266px;
    height: auto;
  }
  .gym__image-video__button {
    width: 52px;
    height: 52px;
  }
  .gym__image-video__button svg {
    width: 12px;
    height: auto;
  }
  .gym__image-video__button::after {
    width: 73px;
    height: 73px;
    mask: radial-gradient(34px, rgba(255, 255, 255, 0) 100%, #ffffff);
  }
  .gym__image-video__button:hover::after,
.gym__image-video__button:focus::after,
.gym__image-video__button:active::after {
    mask: radial-gradient(26px, rgba(255, 255, 255, 0) 100%, #ffffff);
  }
}
@media (max-width: 767px) {
  .gym {
    padding-top: 25px;
    padding-bottom: 70px;
  }
  .gym__container {
    padding: 0;
    gap: 34px;
  }
  .gym__content {
    padding: 0 15px;
    text-align: start;
  }
  .gym__content h2 {
    margin-left: 8px;
    margin-bottom: 4px;
    font-size: 32px;
    line-height: 38px;
  }
  .gym__content h4 {
    margin-top: 4px;
    margin-bottom: 24px;
    font-size: 18px;
    line-height: 27px;
    transform: translate(0, 0);
  }
  .gym__content p {
    margin: inherit;
    width: 90%;
  }
  .gym__image {
    display: flex;
    flex-direction: column;
    gap: 39px;
  }
  .gym__image-video {
    transform: translate(0, 0%);
  }
  .gym__image-video figure {
    margin: inherit;
    width: 100%;
  }
  .gym__image-video img {
    width: 100%;
    height: auto;
  }
